.border{
    position: relative;
    padding: 50px;
    margin-bottom: 50px;
    width: 100%;
    border: 3px solid $indigo-dye;
    &--white{
        border-color: white;
    }
    @include media-d-m($screen-tablet-small){
        padding: 50px 3% 3% 3%;
        margin-bottom: 0;
      }
}